@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Playfair+Display&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1.2rem;
}

body {
  font-family: "Montserrat";
  padding: 15px;
}

.bold {
  font-weight: bold;
}

.generation-wrapper {
  white-space: nowrap;
}
.generation {
  display: inline-flex;
  flex-direction: column;
  flex-wrap: nowrap;
  white-space: nowrap;
  padding: 20px;
  border-right: 35px solid lightgrey;
}

.family-member {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 100px;
  background: lightgrey;
  border: 2px solid black;
  padding: 10px;
  margin: 10px;
  white-space: normal;
}

.more {
  position: absolute;
  right: 0px;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
}
.more::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 4px;
  top: 50%;
  right: 5px;
  transform-origin: right;
  transform: rotateZ(45deg);
  background: white;
}
.more::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 4px;
  transform-origin: right;
  top: calc(50% - 2px);
  right: 5px;
  transform: rotateZ(-45deg);
  background: white;
}

.couple {
  margin-left: 10px;
  margin-right: 10px;
}

.spouse {
  background: #dfc182;
}
.deceased::after {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: rgba(46, 46, 46, 0.253);
}

.title {
  font-family: "Playfair Display";
  font-size: 2.5rem;
  text-decoration: underline;
}
